.Main {
  display: flex;
  height: 100%;
  width: 90%;
  margin: 0 auto;
  padding-top: 30px;
  background-color: #e8ebf4;
}

.Main-card {
  display: grid;
  height: 200px;
  width: 30%;
  border-radius: 10px;
  background-color: white;
}

.Main-title {
  display: flex;
  height: 60px;
  align-items: center;
  justify-content: space-around;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background: linear-gradient(
    90deg,
    rgba(74, 40, 100, 1) 0%,
    rgba(203, 61, 83, 1) 100%,
    rgba(239, 67, 78, 1) 100%
  );
}

.Main-card__icon {
  width: 30px;
  height: 30px;
  margin-left: 5px;
}

.Main-card__text {
  color: white;
}

.Main-card__dates {
  display: grid;
  justify-content: center;
  align-items: center;
  padding: 0px;
}

.Main-card__cash {
  display: flex;
  justify-content: center;
  font-size: 28px;
  font-weight: 700;
  color: #99566f;
}

.Main-card__date {
  display: flex;
  font-size: small;
  justify-content: center;
  margin-top: -2px;
  color: #111e6c;
}

.Main-buttons {
  width: 70%;
  margin-left: 50px;
}

.Main-buttons__list {
  display: flex;
  height: 50px;
  justify-content: space-around;
  align-items: center;
  background-color: white;
}

.Main-buttons__list button {
  height: 50px;
  width: 150px;
  background-color: white;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 700;
  border: transparent;
  cursor: pointer;
  color: #111e6c;
}

.Main-buttons__list button:hover {
  background-color: #613cb66e;
}

.Main-buttons__filter {
  display: flex;
  height: 40px;
  margin-top: 10px;
  justify-content: flex-end;
}

.Main-buttons__filter button {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  margin: 0px;
  width: 150px;
  background-color: white;
  border: transparent;
  cursor: pointer;
}

.filter {
  margin-left: 5px;
  height: 20px;
  width: 20px;
}
