.table {
  width: 90%;
  margin: 0 auto;
  border: 1px solid #969696;
}

.table-files th {
  color: #323232;
  border-bottom: 1px solid #969696;
  padding-bottom: 10px;
}

.table-columns td {
  border-bottom: 1px solid #969696;
}

.table tr {
  height: 55px;
}

.table-columns td p {
  display: flex;
  justify-content: center;
}

.table-columns3 td p {
  display: flex;
  justify-content: center;
}

.table-columns3 {
  border-bottom: 1px solid #969696;
}

.table-title {
  display: flex;
  background: linear-gradient(
    90deg,
    rgba(17, 30, 108, 1) 17%,
    rgba(47, 35, 104, 1) 39%,
    rgba(62, 38, 102, 1) 51%,
    rgba(74, 40, 100, 1) 69%,
    rgba(239, 67, 78, 1) 100%
  );
  height: 60px;
  width: 90%;
  margin: 0 auto;
  margin-top: 30px;
  align-items: center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.table-title h3 {
  margin-left: 10px;
  color: white;
}

.icon-cobro,
.icono-metodo {
  width: 20px;
  height: 20px;
}

.table-transaccion,
.table-metodo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.table-transaccion img {
  margin-right: 5px;
}

.table-metodo img {
  margin-right: 5px;
}
